$color: steelblue;

.background {
  background-image: url('./img/background.jpg')
}

body {
  height: 97vh;
}

.my-flix {
  color: $color;
}

.form-label {
  font-size: larger;
  text-decoration: underline;
  color: rgb(0, 0, 0);
}

img {
  max-width: auto;
  max-height: auto;
}

button {
  border-radius: 8px;
  background-color: black;
  transition-duration: 0.4s;
  color: white;
}

button:hover {
  border-radius: 4px;
  background-color: gray;
  cursor: pointer;
  ;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 667px;
  max-height: auto;
  color: #707171;
}

.movie-poster {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 1667px;
  max-height: auto;
  color: gray;
}

.movie-title {
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
  background-color: lightgray;
  max-width: 667px;
}

.movie-description {
  max-width: 667px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
  background-color: lightgray;
}

.label {
  font-weight: 500;
}

.card {
  height: 574.23px;
  width: 395px;
}

div.container {
  max-width: 1500px;
}